main {
    text-align: center;
    padding: 2rem;
    h1,span {
        text-transform: uppercase;
        color: $turquesa;
        font-family: $fuente_principal;
        margin: 0;
    }
    h1 {
        font-size: 9rem;
        line-height: 1;
        margin-bottom: 1rem;
        @include tablet {
            font-size: 10.4rem;
        }
    }
    span {
        font-size: 6rem;
        line-height: 1;
        @include tablet {
            font-size: 7rem;
        }
    }
}

.descripcion {
    padding: 2rem;
    background-color: $turquesa;
    p {
        font-family: $fuente_secundaria;
        font-size: 4rem;
        text-align: center;
    }
}

.iconos-taller {
    @include tablet {
        display: flex;
        flex-direction: column;
    }
    padding: 2rem;
    p {
        line-height: 1.2;
        margin-bottom: 10rem;
        font-size: 3rem;
        span {
            font-weight: bold;
            display: block;
        }
    }

    .icono {
        text-align: center;
        margin-bottom: 10rem;
        flex: 1;
        img {
            height: 20rem;
        }
        p {
            margin-top: 3rem;
            margin-bottom: 0;
            
        }
    }

}

.flex {
    @include tablet {
        display: flex;
        justify-content: space-between;
    }
}

.whatsapp {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: $turquesa;
    margin-bottom: 4rem;
    a {
        font-size: 4rem;
        text-transform: inherit;
        font-family: 'Arial';
        color: $azul;
        font-weight: $bold;
    }
}

.icono-whatsapp {
    padding: 3rem;
    background-color: $azul;
    border-radius: 4rem;
    width: 25rem;
    img {
        height: 20rem;
    }
}

.modalidad {
    display: flex;
    justify-content: center;
    padding: 4rem;
    h2,p,span {
        font-family: 'Arial';
        text-transform: inherit !important;
        color: $turquesa;
        margin: 0;
        text-align: left;
        font-size: 2.2rem;
        line-height: 1.3;
    }
    h2 {
        font-size: 2.4rem;
        font-weight: $bold;
        margin-bottom: 3rem;
    }
    p {
        margin-bottom: 3rem;
        &:last-of-type {
            margin-bottom: 0;
        }
    }
    span {
        display: block;
    }
}

.auspiciantes {
    background-color: $blanco;
    display: flex;
    justify-content: center;
    align-items: center;
    a {
        flex: 1;
    }
}