.inicio {
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    max-width: 100rem;
    margin: 0 auto;
    min-height: 30rem;
    @include tablet(){
        min-height: 61.2rem;
    }
    .header-contenido {
        @include tablet(){
            min-height: 61.2rem;
        }
    }
}

.no-webp .inicio{
    background-image: url('../img/Curso-Imagen-landing.jpg');
}
.webp .inicio {
    background-image: url('../img/Curso-Imagen-landing.webp');
}
.header {
    padding-top: 3.5rem;
}