.seccion {
    margin-top: $separacion / 3;
    margin-bottom: $separacion / 3;
}
.contenido-centrado {
    max-width: 800px;
}

.alinear-derecha {
    display: flex;
    justify-content: flex-end;
}

.w-100 {
    width: 100%;
}

.center {
    text-align: center !important;
}

.ocultar {
    display: none;
}

.fixed {
    position: fixed;
    top: 40%;
    left: 25%;
    right: 25%;
}

#emergente {
    opacity: 0.9;
    background-color: white;
    padding: 3rem;
    border: 1px solid $negro;
    box-shadow: 0 0 0.3rem $negro;
}